.sort{
  position: absolute;
  left:50%;
  transform: translate(-48%, -50%);
  margin:0 auto;
  top:180px;
  display: grid; 
  grid-template-columns : 90px  90px 90px 66px 30px;
  @media screen and (min-width: 1024px){
    transform: translate(-50%, -50%);
    top : 300px;
    grid-template-columns : 98px 98px 98px 502px 30px;
    grid-gap : 20px;
  }
}
