  top:300px;
  width : 355px;
  height:250px;
  background-color: #EDF1D5;
  border-radius: 20px;
  z-index: 1;
  box-sizing: border-box;
  @media screen and (min-width: 1024px){
  width:500px;
  top:500px;

  }
}
